.header {
    top: 0;
    position: sticky;
    display: flex;
    padding: 15px 20px;
    justify-content: space-between;
    background-color: #fff;
    box-shadow: 0 5px 8px -9px rgba(0, 0, 0, 0.75);
    z-index: 100;
}
.header__left {
    display: flex;
    justify-content: space-evenly;
}
.header__input {
    display: flex;
    align-items: center;
    padding: 10px;
    margin-left: 10px;
    background-color: #eff2f5;
    border-radius: 999px;
}
.header__left > img {
  height: 40px;
}
.header__input > input {
    border: none;
    background-color: transparent;
    outline-width: 0;
}
.header__middle {
    display: flex;
    flex: 1;
    justify-content: center;
}
.header__option {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 0 30px;
}
.header__option--active {
    border-bottom: 4px solid #2e81f4;
}
.header__option > .MuiSvgIcon-root {
    color: #bebebe;
}
.header__option:hover > .MuiSvgIcon-root {
    color: #2e81f4;
    background-color: #eff2f5;
    border-radius: 10px;
    align-items: center;
    padding: 0 30px;
    border-bottom: none;
}
.header__info {
    display: flex;
    align-items: center;
}
.header__info > h4 {
    margin-left: 10px;
}
.header__right {
    display: flex;
}
